<template>
  <div class="replace">
    <p class="title">Replace</p>
    <hr>
    <div class="row w-50">
      <div class="col">
        <input type="text" v-model="txt" class="form-control w-100" placeholder="文字列">
      </div>
      <div class="col">
        <input type="text" v-model="preRep" class="form-control w-100" placeholder="置換前">
      </div>
      <div class="col">
        <input type="text" v-model="rep" @change="repTxt" class="form-control w-100" placeholder="置換後">
      </div>
    </div>

    <div class="row w-50 mt-3 mb-3">
      <div class="col">
        <textarea class="form-control" v-model="res" rows="10" placeholder="結果"></textarea>
      </div>
    </div>

    <p>{{msg}}</p>
  </div>
</template>

<script>
export default {
  name: 'Replace',
  data() {
    return {
      txt: '',
      preRep: '',
      rep: '',
      res: '',
      msg: '',
    };
  },
  methods: {
    repTxt() {
      this.res =  this.txt.replace(new RegExp(this.preRep, 'g'), this.rep);
      navigator.clipboard.writeText(this.res)
      .then(() => {
        this.msg = "copied!";
      })
    },
  },
}
</script>


<style scoped>
.title{
  font-size: 2rem;
  font-weight: 100;
}
</style>
